// src/ui/components/PoseOverlay.tsx ()

import React from 'react';
import { Landmark } from '../../types';

interface PoseOverlayProps {
  landmarks: Landmark[];
  videoWidth: number;
  videoHeight: number;
  containerWidth?: number;
  containerHeight?: number;
  isMirrored?: boolean;
}

export const PoseOverlay: React.FC<PoseOverlayProps> = ({
  landmarks,
  videoWidth,
  videoHeight,
  containerWidth,
  containerHeight,
  isMirrored = false,
}) => {
  if (!landmarks || landmarks.length === 0 || videoWidth === 0 || videoHeight === 0) {
    return null;
  }

  // 
  const displayWidth = containerWidth || videoWidth;
  const displayHeight = containerHeight || videoHeight;

  // 
  const transformLandmark = (landmark: Landmark) => {
    let x = landmark.x * displayWidth;
    let y = landmark.y * displayHeight;
    
    if (isMirrored) {
      x = displayWidth - x;
    }
    
    return { x, y, visibility: landmark.visibility };
  };

  // 
  const importantLandmarks = [
    0,   // 
    11, 12, // 
    23, 24, // 
    25, 26, // 
    27, 28, // 
    15, 16, // 
  ];

  // 
  const connections = [
    [11, 12], // 
    [11, 23], // -
    [12, 24], // -
    [23, 24], // 
    [11, 13], // -
    [13, 15], // -
    [12, 14], // -
    [14, 16], // -
    [23, 25], // -
    [25, 27], // -
    [24, 26], // -
    [26, 28], // -
    [0, 11],  // -
    [0, 12],  // -
  ];

  const visibleLandmarks = landmarks.filter((landmark, index) => 
    landmark && 
    typeof landmark.visibility === 'number' && 
    landmark.visibility > 0.5 &&
    importantLandmarks.includes(index)
  );

  return (
    <div
      className="absolute inset-0 pointer-events-none"
      style={{
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        zIndex: 999999, // 
        pointerEvents: 'none',
      }}
    >
      <svg
        width="100%"
        height="100%"
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          background: 'rgba(0,255,0,0.2)', // 
        }}
        viewBox={`0 0 ${displayWidth} ${displayHeight}`}
        preserveAspectRatio="none"
      >
        {/*  */}
        <circle
          cx={displayWidth / 2}
          cy={displayHeight / 2}
          r="50"
          fill="red"
          opacity="1"
        />
        
        {/*  */}
        <text
          x={displayWidth / 2}
          y={50}
          textAnchor="middle"
          fill="yellow"
          fontSize="24"
          fontWeight="bold"
          stroke="black"
          strokeWidth="1"
        >
          SVG OVERLAY VISIBLE! {visibleLandmarks.length} landmarks
        </text>
        
        {/*  */}
        {connections.map(([startIdx, endIdx], connectionIndex) => {
          const startLandmark = landmarks[startIdx];
          const endLandmark = landmarks[endIdx];
          
          if (!startLandmark || !endLandmark ||
              startLandmark.visibility < 0.5 || endLandmark.visibility < 0.5) {
            return null;
          }
          
          const start = transformLandmark(startLandmark);
          const end = transformLandmark(endLandmark);
          
          return (
            <line
              key={`connection-${connectionIndex}`}
              x1={start.x}
              y1={start.y}
              x2={end.x}
              y2={end.y}
              stroke="#00ff00"
              strokeWidth="4"
              opacity="1"
            />
          );
        })}
        
        {/*  */}
        {visibleLandmarks.map((landmark) => {
          const originalIndex = importantLandmarks.find(idx => landmarks[idx] === landmark);
          if (originalIndex === undefined) return null;
          
          const point = transformLandmark(landmark);
          const radius = 8; // 
          
          // 
          let color = '#ff0000'; // 
          if ([11, 12].includes(originalIndex)) color = '#ff6600'; // 
          if ([23, 24].includes(originalIndex)) color = '#0066ff'; // 
          if ([25, 26].includes(originalIndex)) color = '#ff00ff'; // 
          if ([27, 28].includes(originalIndex)) color = '#00ffff'; // 
          if (originalIndex === 0) color = '#ffff00'; // 
          
          return (
            <circle
              key={`landmark-${originalIndex}`}
              cx={point.x}
              cy={point.y}
              r={radius}
              fill={color}
              opacity="1"
              stroke="#ffffff"
              strokeWidth="2"
            />
          );
        })}
      </svg>
    </div>
  );
};
